@tailwind base;
@tailwind components;
@tailwind utilities;

/* Import Inter font with all weights and styles */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* Font family usage */
body {
  font-family: 'Inter', sans-serif;
}

/* Add this to your CSS or global stylesheet */
.scrollbar-hidden::-webkit-scrollbar {
  display: none; /* Hides scrollbar in Webkit browsers (e.g., Chrome, Safari) */
}

.scrollbar-hidden {
  -ms-overflow-style: none; /* Hides scrollbar in Internet Explorer */
  scrollbar-width: none; /* Hides scrollbar in Firefox */
}

/* global.css */
html, body { /* Set your desired background color */
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
}